<template>
  <div>
    <h1>Místnosti</h1>
    <table>
      <tr v-for="room in rooms">
        <td>
          <router-link :to="{name: 'room', params: {id: room.id_rooms}}">
            {{room.title}}
          </router-link>
        </td>
      </tr>
    </table>
    <hr>
    <new-room></new-room>
    <!--<button v-on:click="loadRooms">Obnovit</button>-->
  </div>
</template>

<script>
import NewRoom from '@/views/NewRoom';

export default {
    components: {NewRoom},
    data() {
        return {
            rooms: []
        }
    },
    mounted() {
        this.loadRooms();
    },
    methods: {
        loadRooms() {
            this.$http.get('api/auth/rooms')
                .then(response => {
                    this.rooms = response.data;
                });
        }
    }
}
</script>



